.movie-details {
  &__parts {
    display: flex;
    flex-direction: column-reverse; }
  &__poster {
    @include img-hover;
    img {
      max-height: 350px; } }
  &__link {
    font-size: 16px;
    @include line-hover; }
  &__desc {
    margin-top: 20px; }
  &__props {
    max-width: 700px;
    font-size: 14px;
    line-height: 1.25;
    color: rgba($color-text, .7);
    margin: 20px 0;
    td {
      padding: 3px 5px;
      vertical-align: top;
      &:first-child {
        padding-left: 0;
        max-width: 150px; }
      &:last-child {
        padding-right: 0; } } } }

@media only screen and (min-width: 500px) {
  .movie-details {
    &__parts {
      display: block;
      font-size: 0; }
    &__const,
    &__var {
      display: inline-block;
      vertical-align: top; }
    &__const {
      position: relative;
      width: 170px;
      margin-top: 0;
      margin-right: -170px;
      img {
        max-height: none; } }
    &__var {
      width: 100%;
      padding-top: 10px;
      padding-left: 185px; } } }

@media only screen and (min-width: 768px) {
  .movie-details {
    &__const,
    &__const {
      width: 300px;
      margin-right: -300px; }
    &__var {
      padding-left: 320px; }
    &__props {
      font-size: 16px; } } }
